body{
  
  background-image: url('https://cdn25.picsart.com/144261008000202.gif');
    background-size: cover;
    
    
    
    height: 100vh;
    padding:0;
    margin:0;
}

.App
{
  background: #1c92d2;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #f2fcfe, #1c92d2);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to left, #f2fcfe, #1c92d2); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */


  min-height : 500px;
  width: 400px;
  margin: 40px auto;
}

#to-do-form input
{

  background: #000000;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #434343, #000000);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #434343, #000000); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  border:0;
  width:200px;
  height:50px;
  padding:0 20px;
  margin:20px;
  color:#fff;
}
#to-do-form input::placeholder
{
color : rgba(255,255,255,0.5);

}
#to-do-form input:focus,#to-do-form button:focus{
outline : none;
}

#to-do-form button
{
  height:50px;
  width:80px;
  border-radius: 5px;
  border : 0;
  background: #20002c;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #cbb4d4, #20002c);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #cbb4d4, #20002c); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  
  cursor:pointer;
  font-weight: bolder;
  color:#fff;

}